import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { DistrictService } from 'src/app/WebService/district.service';
import { } from 'googlemaps';

import { map } from 'rxjs/operators'
import { FormGroup, FormControl } from '@angular/forms';
import { RegistrationService } from 'src/app/WebService/Master/registration.service';

@Component({
  selector: 'app-owner-registration',
  templateUrl: './owner-registration.component.html',
  styleUrls: ['./owner-registration.component.css']
})
export class OwnerRegistrationComponent implements OnInit {
  latlng=true;@ViewChild('map', { static:true}) mapElement: ElementRef;map: google.maps.Map;vehicleOwnerForm:FormGroup;
  stateArr=[];district=[];talukaArr:any=[];districtArr:any=[];jsonObj=[];dataSource=[];
  userinfo:Storage;  SerahForm:FormGroup;SaveUpdate='Save';dataSourceflag:boolean=false;
   constructor( private distsrvc:DistrictService,private registrationService:RegistrationService) { }
    ngOnInit() {
      
     this.userinfo=JSON.parse(localStorage.info).checkLogin[0]
     this.distsrvc.getState(this.userinfo.UserId).pipe(map(responseData => {let resData=[];for(const key in responseData){resData=responseData[key];}return resData;})).subscribe(dt => {this.stateArr=dt})
   
     this.defaultForm();
      this.SerahForm=new FormGroup({StateId:new FormControl(1),DistrictId:new FormControl(0),Search:new FormControl(''),UserId:new FormControl(this.userinfo.UserId),PageNo:new FormControl(1)})
      if(this.SerahForm.value.StateId==0){ this.SerahForm.patchValue({DistrictId:0});this.district=[] } else{ let o={"UserId": this.userinfo.UserId,"StateId": this.SerahForm.value.StateId}; this.distsrvc.getdistrict(o).pipe(map(responseData => {let resData=[];for(const key in responseData){resData=responseData[key];}return resData;})).subscribe(dt => {this.district=dt;}) }
      this.bindData()
    }
    
    bindData(){
      debugger
      let obj=this.SerahForm.value
this.registrationService.getVehicleOwnerDetails(obj)
  .pipe(
    map(responseData => {
      let resData=[];
     if(responseData==null||responseData==''){return resData }else{
     for(const key in responseData){resData.push(responseData[key])}return resData;}
    })
    )
  .subscribe(dt => {
    debugger
    if(dt.length){this.jsonObj=dt[1]==''?[]:dt[1];this.dataSource=dt[0];
    this.dataSourceflag=true;
  
  }else{
    this.dataSourceflag=false;
  }
     })
    }
  onchangeStateBindDist() { if(this.SerahForm.value.StateId==0){ this.SerahForm.patchValue({DistrictId:0});this.district=[] } else{ let o={"UserId": this.userinfo.UserId,"StateId": this.SerahForm.value.StateId}; this.distsrvc.getdistrict(o).pipe(map(responseData => {let resData=[];for(const key in responseData){resData=responseData[key];}return resData;})).subscribe(dt => {this.district=dt;}) } }
  hideModal():void {document.getElementById('myModal').click();this.refreshContent()}// for hide popUp
 
  onSubmit(){
    debugger
    if(this.vehicleOwnerForm.valid){
     this.registrationService.saveVehicleOwner(this.vehicleOwnerForm.value) .pipe( map(responseData => { let resData=[]; for(const key in responseData){ resData=responseData[key]; }return resData;})) .subscribe(resdata => { 
       alert(resdata[0].Msg);this.bindData();this.hideModal(); } )
   }else{alert('Please fill the mandatory field ')} }

  onchangeStateBindDistData() { if(this.vehicleOwnerForm.value.StateId==0){ this.vehicleOwnerForm.patchValue({DistrictId:0});this.districtArr=[]; }else{ let o={"UserId": this.userinfo.UserId,"StateId": this.vehicleOwnerForm.value.StateId}; this.distsrvc.getdistrict(o).pipe(map(responseData => {let resData=[];for(const key in responseData){resData=responseData[key];}return resData;})).subscribe(dt => { this.districtArr=dt;}) } }
  OpenModel(){}
  refreshContent(){  this.defaultForm();; this.SaveUpdate='Save'; }
  Edit(ev,rn){
    debugger
    this.SaveUpdate='Update'
    let o=this.dataSource.find(function(cr){return cr.RowNumber==rn})
    
  //  o.CreatedBy=this.userinfo.UserId
    this.vehicleOwnerForm.patchValue({ 
       Name: o.Name,
      MobileNo: o.MobileNo,
      EmailId: o.EmailId,
      Address:o.Address, 
      StateId:o.StateId,
      GSTNo:o.GSTNo, 
      Longitude:o.Longitude,
     Latitude:o.Longitude,
     DistrictId:o.DistrictId,
     OwnerCompany:o.OwnerCompany,
     VehicleOwnerId:o.VehicleOwnerId,
     CreatedBy:this.userinfo.UserId,
    });
     this.onchangeStateBindDistData();
   
  }
  defaultForm(){

    this.vehicleOwnerForm= new FormGroup({
      Name: new FormControl(null),
      MobileNo: new FormControl(null),
      EmailId: new FormControl(null),
      Address:new FormControl(null), 
      StateId:new FormControl(''),
      GSTNo:new FormControl(null), 
      Longitude:new FormControl(null),
     Latitude:new FormControl(null),
     DistrictId:new FormControl(''),
     OwnerCompany:new FormControl(null),
     VehicleOwnerId:new FormControl(0),
     CreatedBy:new FormControl(this.userinfo.UserId),SaveUpdate:new FormControl('Save')
    })
  }
}
